.header__link {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.22;

  color: #FFFFFF;
  text-decoration: none;
}

.header__link:hover {
  opacity: .6;
}

@media screen and (max-width: 768px) {
  .header__link {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.21;
  }
}